@font-face {
    font-family: Montserrat;
    font-weight: 800;
    src: url('./Components/assets/fonts/Montserrat-VariableFont_wght.ttf');
}

@font-face {
    font-family: Montserrat;
    font-weight: 500;
    src: url('./Components/assets/fonts/Montserrat-VariableFont_wght.ttf');
}


@font-face {
    font-family: Montserrat;
    font-weight: 400;
    src: url('./Components/assets/fonts/Montserrat-VariableFont_wght.ttf');
}


@font-face {
    font-family: Montserrat;
    font-weight: 300;
    src: url('./Components/assets/fonts/Montserrat-VariableFont_wght.ttf');
}

*{
    margin: 0%;
    padding: 0%;
    border: 0;
    outline: none;
    list-style: none;
    text-decoration: none;
}

body,html,#root{
    width: 100vw;
    height: 100vh;
}

.bg{
    position: absolute;
    left: 0%;
    top: 0%;
    width: 100%;
    height: 100%;
    background: no-repeat hsla(236, 72%, 79%,0.1);
}

.topImg{
    position: absolute;
    left: calc(100vw - 375px);
    height: 100%;
}

.bottomImg{
    position: absolute;
    top: calc(100vh - 304px);
}


@media screen and (max-width: 999px) {
    .bottomImg{
        display: none;

    }

    body,html,#root{
        width: 100vw;
        height: max-content;
    }

    .topImg{
        display: none;
    }
}